<template lang="html">
  <div class="swipe">
    <swiper :options="swiperOption" ref="mySwiper" class="bg-div" :style="{marginTop: top}">
      <swiper-slide><img src="static/imgs/banner1.jpg" alt=""></swiper-slide>
      <swiper-slide><img src="static/imgs/banner2.jpg" alt=""></swiper-slide>
      <swiper-slide><img src="static/imgs/banner3.jpg" alt=""></swiper-slide>
      <swiper-slide><img src="static/imgs/banner4.jpg" alt=""></swiper-slide>

      <!-- Optional controls -->
      <div class="swiper-pagination"  slot="pagination"></div>
      <!-- <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div>
      <div class="swiper-scrollbar"   slot="scrollbar"></div> -->
    </swiper>
  </div>
</template>

<script>


export default {
  name: 'Gallery',
  data() {
      return {
        swiperOption: {
          // notNextTick
          // notNextTicktrueNextTickswiper
          // swiper
          // swipertrue
          notNextTick: true,
          // swiper configs swiperapi
          autoplay: 3000,
          speed: 500,
          loop: true,
          effect : 'slide',
          direction : 'horizontal',
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          // prevButton:'.swiper-button-prev',
          // nextButton:'.swiper-button-next',
          // scrollbar:'.swiper-scrollbar',
          mousewheelControl : true,
          observeParents:true,
          // 
          // debugger
          debugger: true,
          // swiper callbacks
          // swiperswiper
          // onTransitionStart(swiper){
          //   console.log(swiper)
          // },
        }
      }
    },
    props: ['top']
    // swiper
    // swiper
    // notNextTicktrue
    // computed: {
    //   swiper() {
    //     return this.$refs.mySwiper.swiper
    //   }
    // },
    // mounted() {
    //   // swiper
    //   console.log('this is current swiper instance object', this.swiper)
    //   // this.swiper.slideTo(3, 1000, false)
    // }
}

</script>

<style lang="css" scoped>
/*.swipe{
  margin-top: -50px;
}*/
span{
  z-index: 100;
}
.bg-div {
  background-color: #ff2d4b;
  color: #fff;
}
.swiper-slide{
    width: 100%;
    float: left;
}
img{
  width: 100%;
}
</style>



